import React from 'react';
import { Routes, Route } from 'react-router-dom';
import ProfilePage from './tabs/Profile';
import Courses from './tabs/Courses';
import Main from './tabs/Main';
import CurrentLesson from './tabs/CurrentLesson';
import LessonPage from './tabs/lesson/LessonPage';
import WorkbookPage from './tabs/lesson/WorkbookPage';
import AdminPage from './AdminPage';

const AppRouter = () => {
  return (
    <Routes>
      <Route path="/" element={<Main />} />
      <Route path="/profile" element={<ProfilePage />} />
      <Route path="/courses" element={<Courses />} />
      <Route path="/lesson" element={<CurrentLesson />} />
      <Route path="/course/:courseID/module/:moduleID/lesson/:lessonID" element={<LessonPage />} />
      <Route path="/course/:courseID/module/:moduleID/workbook/:workbookID" element={<WorkbookPage />} />
      <Route path="/admin" element={<AdminPage />} />
    </Routes>
  );
};

export default AppRouter;
