import { kebabCase } from 'lodash'; 

export const PART_KEYS = [
  'Part1/Type', 'Part1/A.Warm-up', 'Part1/A.task', 'Part1/A.kit', 'Part1/A.pic', 'Part1/A.article', 'Part1/A.audio', 'Part1/A.hint',
  'Part1/B.task', 'Part1/B.kit', 'Part1/C.task', 'Part1/C.kit', 'Part1/D.task', 'Part1/D.kit',
  'Part2/Type', 'Part2/A.task', 'Part2/A.kit', 'Part2/A.pic', 'Part2/A.article', 'Part2/A.audio', 'Part2/A.hint',
  'Part2/B.task', 'Part2/B.kit', 'Part2/C.task', 'Part2/C.kit', 'Part2/D.task', 'Part2/D.kit',
  'Part3/Type', 'Part3/A.task', 'Part3/A.kit', 'Part3/A.pic', 'Part3/A.article', 'Part3/A.audio', 'Part3/A.hint',
  'Part3/B.task', 'Part3/B.kit', 'Part3/C.task', 'Part3/C.kit', 'Part3/D.task', 'Part3/D.kit',
  'Part4/Type', 'Part4/A.task', 'Part4/A.kit', 'Part4/A.pic', 'Part4/A.article', 'Part4/A.audio', 'Part4/A.hint',
  'Part4/B.task', 'Part4/B.kit', 'Part4/C.task', 'Part4/C.kit', 'Part4/D.task', 'Part4/D.kit',
  'Part5/Type', 'Part5/A.task', 'Part5/A.kit', 'Part5/A.pic', 'Part5/A.article', 'Part5/A.audio', 'Part5/A.hint',
  'Part5/B.task', 'Part5/B.kit', 'Part5/C.task', 'Part5/C.kit', 'Part5/D.task', 'Part5/D.kit'
];

export const handleInteraction = () => {
  // Function to find the correct parent with a `data-correct` attribute
  const findCorrectParent = (element) => {
    while (element) {
      if (element.hasAttribute('data-correct')) {
        return element;
      }
      element = element.parentElement;
    }
    return null;
  };

  // Handler for text inputs
  const handleTextInputChange = (e) => {
    const inputElement = e.target;
    const correctValue = inputElement.getAttribute('data-correct');
    const selectedValue = inputElement.value;

    const kebabCorrectValue = kebabCase(correctValue);
    const kebabSelectedValue = kebabCase(selectedValue);

    if (kebabSelectedValue === kebabCorrectValue) {
      inputElement.classList.add('correct');
      inputElement.classList.remove('incorrect');
      inputElement.style.backgroundColor = 'lightgreen'; // Correct answer background color
    } else {
      inputElement.classList.add('incorrect');
      inputElement.classList.remove('correct');
      inputElement.style.backgroundColor = 'lightcoral'; // Incorrect answer background color
    }
  };

  // Handler for radio buttons and selects
  const handleChange = (e) => {
    let correctValue;
    let correctParent = e.target;

    // Check if data-correct is on the element itself or on a parent
    if (e.target.tagName === 'SELECT' && e.target.hasAttribute('data-correct')) {
      correctValue = e.target.getAttribute('data-correct');
    } else {
      correctParent = findCorrectParent(e.target);
      if (!correctParent) return;
      correctValue = correctParent.getAttribute('data-correct');
    }

    const selectedValue = e.target.value;

    if (selectedValue === correctValue) {
      correctParent.classList.add('correct');
      correctParent.classList.remove('incorrect');
      correctParent.style.backgroundColor = 'lightgreen';
    } else {
      correctParent.classList.add('incorrect');
      correctParent.classList.remove('correct');
      correctParent.style.backgroundColor = 'lightcoral';
    }
  };

  // Handler for button clicks
  const handleButtonClick = (e) => {
    if (e.target.tagName === 'BUTTON') {
      const correctParent = findCorrectParent(e.target);
      if (correctParent) {
        const correctValue = correctParent.getAttribute('data-correct');
        const clickedValue = e.target.id;

        if (clickedValue === correctValue) {
          e.target.classList.add('correct');
          e.target.classList.remove('incorrect');
        } else {
          e.target.classList.add('incorrect');
          e.target.classList.remove('correct');
        }
      }
    }
  };

  const addEventListeners = () => {
    const textInputs = document.querySelectorAll('input[type="text"][data-correct]');
    textInputs.forEach(input => {
      input.addEventListener('input', handleTextInputChange);
    });

    const radioInputs = document.querySelectorAll('input[type="radio"]');
    radioInputs.forEach(input => {
      input.addEventListener('change', handleChange);
    });

    const selects = document.querySelectorAll('select');
    selects.forEach(select => {
      select.addEventListener('change', handleChange);
    });

    document.addEventListener('click', handleButtonClick);
  };

  addEventListeners();

  // Combined dynamic event listener for input, radio, and select elements
  document.addEventListener('input', (e) => {
    if (e.target.matches('input[type="text"][data-correct]')) {
      handleTextInputChange(e);
    } else if (e.target.matches('input[type="radio"]')) {
      handleChange(e);
    } else if (e.target.matches('select')) {
      handleChange(e);
    }
  });

  return () => {
    const textInputs = document.querySelectorAll('input[type="text"][data-correct]');
    textInputs.forEach(input => {
      input.removeEventListener('input', handleTextInputChange);
    });

    const radioInputs = document.querySelectorAll('input[type="radio"]');
    radioInputs.forEach(input => {
      input.removeEventListener('change', handleChange);
    });

    const selects = document.querySelectorAll('select');
    selects.forEach(select => {
      select.removeEventListener('change', handleChange);
    });

    document.removeEventListener('click', handleButtonClick);
    document.removeEventListener('input', handleTextInputChange);
    document.removeEventListener('input', handleChange);
  };
};
