import React from 'react';
import { useAuth } from '../contexts/AuthContext';
import '../styles/Profile.css';
import { useNavigate } from 'react-router-dom';

const ProfilePage = () => {
  const { user, logout } = useAuth();
  const navigate = useNavigate();

  // Handle user logout and navigation
  const handleLogout = async () => {
    try {
      await logout();
      navigate('/index');
    } catch (error) {
      console.error('Failed to logout:', error.message);
      alert('Failed to logout: ' + error.message);
    }
  };

  if (!user) {
    return <div className='loader'>Loading...</div>; // Optionally redirect to sign-in page if user is not authenticated
  }

  return (
    <div className="profile-page">
      <ProfileSidebar handleLogout={handleLogout} />
      <ProfileMainSection user={user} />
    </div>
  );
};

// Sidebar component with fixed position for logout button
const ProfileSidebar = ({ handleLogout }) => (
  <aside className="sidebar">
    <div className="sidebar-set">
      <SidebarItem icon="person" text="Info" />
      <SidebarItem icon="chevron_right" text="How2Start" />
      <SidebarItem icon="book_5" text="Tests" />
      <SidebarItem icon="people" text="Friends" />
      <SidebarItem icon="account_balance" text="Balance" />
      <SidebarItem icon="monitoring" text="Stats" />
      <SidebarItem icon="settings" text="Settings" />
      <button className="sidebar-items logout-button" onClick={handleLogout}>
        <span className="material-symbols-outlined">logout</span>
        <span className="sidebar-text">EXIT</span>
      </button>
    </div>
  </aside>
);

const SidebarItem = ({ icon, text }) => (
  <div className="sidebar-items">
    <span className="material-symbols-outlined">{icon}</span>
    <span className="sidebar-text">{text}</span>
  </div>
);

const ProfileMainSection = ({ user }) => (
  <section className="main-section">
    <ProfileHeader user={user} />
    <StartSection />
    <BalanceSection />
    <div>{user.courseID}</div>
    <footer className="development-notice">in development...</footer>
  </section>
);

const ProfileHeader = ({ user }) => (
  <div className="profile-header">
    <div className="profile-info">
      <div className="profile-image-container">
        <div className="profile-image-wrapper">
          <img
            loading="lazy"
            src={user.photoURL || 'default-avatar.png'}
            className="profile-image"
            alt="Profile"
          />
          <div className="profile-name-container">
            <div className="profile-label">Name:</div>
            <div className="profile-name">{user.displayName || 'No Name'}</div>
          </div>
        </div>
      </div>
      <ProfileDetails user={user} />
    </div>
  </div>
);

const ProfileDetails = ({ user }) => (
  <div className="profile-details">
    <ProfileDetail label="ID:" value={user.uid} />
    <ProfileDetail label="Native language:" value={user.nativeLanguage || 'Not specified'} />
    <ProfileDetail label="Job/Study:" value={user.job || 'Not specified'} />
    <ProfileDetail label="E-mail:" value={user.email} />
    <ProfileDetail label="Contacts:" value={user.contacts || 'Not specified'} />
    <ProfileDetail label="Goal:" value={user.goal || 'Not specified'} />
  </div>
);

const ProfileDetail = ({ label, value }) => (
  <div className="detail-item">
    <div className="detail-label">{label}</div>
    <div className="detail-value">{value}</div>
  </div>
);

const StartSection = () => (
  <div className="start-section">
    <div className="start-content">
      <div className="start-text">
        <h2 className="start-heading">LET'S<br />START!</h2>
      </div>
      <div className="steps-content">
        <StepItem step="1" title="Pass a TEST!" description="Get to know your current level!" imgSrc="https://anglomova.com/site/images/test.png" />
        <StepItem step="2" title="Choose course" description="Select course that matches your level" imgSrc="https://anglomova.com/site/images/course.png" />
        <StepItem step="3" title="Pay to start" description="Get the most efficient English lessons after purchase!" imgSrc="https://anglomova.com/site/images/payment.png" />
      </div>
    </div>
  </div>
);

const StepItem = ({ step, title, description, imgSrc }) => (
  <div className="step-item">
    <div className="step-box">
      <span className="step-icon">{step}</span>
      <h2 className="step-title">{title}</h2>
      <p className="step-description">{description}</p>
      <img className="step-img" src={imgSrc} alt={title} />
    </div>
  </div>
);

const BalanceSection = () => (
  <div className="balance-section">
    <BalanceBox />
    <LessonOptions />
    <NextLessonBoxes />
  </div>
);

const BalanceBox = () => (
  <div className="balance-box">
    <div className="balance-wrapper">
      <div className="balance-title">Balance</div>
      <div className="balance-value">value: ___</div>
      <button className="recharge-btn">Recharge</button>
      <div className="crypto-recharge">Recharge with crypto <br />(in progress)</div>
    </div>
  </div>
);

const LessonOptions = () => (
  <div className="lesson-options">
    <div className="options-list">
      <button className="option-item">Videocall</button>
      <button className="option-item">Workbook</button>
      <button className="option-item">Extra</button>
    </div>
  </div>
);

const NextLessonBoxes = () => (
  <>
    <div className="next-lesson-box">
      <div className="next-lesson-text">NEXT<br />LESSON</div>
    </div>
    <div className="next-lesson-box">
      <div className="next-lesson-text">NEXT<br />WB</div>
    </div>
  </>
);

export default ProfilePage;
