import React, { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { useLearning } from '../contexts/LearningContext'; // Assuming you're using LearningContext for course and lesson data
import { useAuth } from '../contexts/AuthContext'; // Assuming you're using AuthContext for user data
import '../styles/CurrentLesson.css';

const CurrentLesson = () => {
  const { user } = useAuth();
  const { selectedCourse, nextLesson, nextWorkbook } = useLearning();
  const [selectedModule, setSelectedModule] = useState(null);
  const [moduleLessons, setModuleLessons] = useState([]);
  const navigate = useNavigate();

  useEffect(() => {
    if (selectedModule && selectedCourse) {
      const lessonsForModule = selectedCourse.lessons.filter(
        lesson => lesson.moduleID === selectedModule.id
      );
      setModuleLessons(lessonsForModule);
    }
  }, [selectedModule, selectedCourse]);

  const handleLessonClick = (lesson) => {
    navigate(`/course/${user.courseID}/module/${lesson.moduleID}/lesson/${lesson.id}`);
  };

  const handleWorkbookClick = (workbook) => {
    navigate(`/course/${user.courseID}/module/${workbook.moduleID}/workbook/${workbook.id}`);
  };

  const handleModuleClick = (module) => {
    setSelectedModule(module);
  };

  const closeOverlay = () => {
    setSelectedModule(null);
  };

  if (!user || !selectedCourse) {
    return <div className="loader">.</div>;
  }

  return (
    <div className="lesson-overview">
      <h1>Current Lesson</h1>
      <div className="wrapper">
        <div>
          {/* Next Lesson Card */}
          <div className="wrapper-component">
            {nextLesson && (
              <div
                className="card"
                onClick={() => handleLessonClick(nextLesson)}
                style={{
                  backgroundImage: `url(${nextLesson.BGimg})`,
                  backgroundSize: 'cover',
                  backgroundPosition: 'center',
                }}
              >
                <div className="module-num top">
                  {nextLesson.courseID} <br />
                  {nextLesson.moduleName}
                </div>
                <h3>
                  Lesson {nextLesson.lessonNum}:<br />
                  {nextLesson.lessonName}
                </h3>
              </div>
            )}
          </div>

          {/* Next Workbook Card */}
          <div className="wrapper-component">
            {nextWorkbook && (
              <div
                className="card"
                onClick={() => handleWorkbookClick(nextWorkbook)}
                style={{
                  backgroundImage: `url(${nextWorkbook.BGimg})`,
                  backgroundSize: 'cover',
                  backgroundPosition: 'center',
                }}
              >
                <div className="module-num top">
                  {nextWorkbook.courseID} <br />
                  {nextWorkbook.moduleName}
                </div>
                <h3>Workbook {nextWorkbook.workbookNum}</h3>
              </div>
            )}
          </div>
        </div>

        {/* Modules List */}
        <div className="wrapper-component right">
          <h2 className="right-cont">Content of {user.courseID}</h2>
          <div className="right-info">
            <div className="modules-list">
              {selectedCourse.modules.map(module => (
                <div
                  key={module.id}
                  className="card-item"
                  onClick={() => handleModuleClick(module)}
                >
                  <img className="listImg" src={module.modImg} alt={module.modName} />
                  <div>{module.modName}</div>
                </div>
              ))}
            </div>
          </div>
        </div>

        {/* Selected Module Details */}
        {selectedModule && (
          <div className="mod">
            <div className="mod-det" onClick={closeOverlay}>
              <div className="mod-det-cont">
                <div className="mod-det-head">
                  <div className="mod-det-head-item">
                    <img className="listImg" src={selectedModule.modImg} alt={selectedModule.modName} />
                    <h3 className="black">{selectedModule.modName}</h3>
                    <button className="clsz" onClick={closeOverlay}>×</button>
                  </div>
                </div>

                {/* Lessons List for Selected Module */}
                <div className="lessons-list">
                  {moduleLessons.map(lesson => (
                    <div
                      key={lesson.id}
                      className="card-item"
                      onClick={() => handleLessonClick(lesson)}
                    >
                      <img className="listImg" src={lesson.BGimg} alt={lesson.lessonName} />
                      <div>
                        {lesson.lessonNum}: {lesson.lessonName}
                      </div>
                    </div>
                  ))}
                </div>

                {/* Module Descriptions */}
                <div
                  className="mod-det-desc long"
                  dangerouslySetInnerHTML={{ __html: selectedModule.modFullDescription }}
                />
                <div
                  className="mod-det-item short"
                  dangerouslySetInnerHTML={{ __html: selectedModule.modDescription }}
                />
              </div>
            </div>
          </div>
        )}
      </div>
    </div>
  );
};

export default CurrentLesson;
