import React, { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';
import DOMPurify from 'dompurify';
import '../../styles/CurrentLesson.css'; 
import '../../styles/Speaking.css'; 
import { handleInteraction, PART_KEYS } from './utils';
import { useLearning } from '../../contexts/LearningContext';

const WorkbookPage = () => {
  const { workbookID } = useParams();
  const { selectedCourse } = useLearning(); 
  const [workbook, setWorkbook] = useState(null);
  const [currentPartIndex, setCurrentPartIndex] = useState(0);
  const [currentPartName, setCurrentPartName] = useState(''); 

  useEffect(() => {
    if (selectedCourse) {
      const workbookData = selectedCourse.workbooks.find(wb => wb.id === workbookID);
      if (workbookData) {
        setWorkbook(workbookData);
        updateCurrentPartName(workbookData, 0); // Initialize the part name

        // Apply dynamic CSS if present
        const sanitizedCSS = DOMPurify.sanitize(workbookData.css || '');
        if (sanitizedCSS) {
          const styleElement = document.createElement('style');
          styleElement.textContent = sanitizedCSS;
          document.head.appendChild(styleElement);

          return () => {
            document.head.removeChild(styleElement);
          };
        }
      }
    }
  }, [selectedCourse, workbookID]);

  useEffect(() => {
    if (workbook) {
      const cleanup = handleInteraction();
      updateCurrentPartName(workbook, currentPartIndex); // Update the part name when the part index changes
      return cleanup;
    }
  }, [workbook, currentPartIndex]);

  const updateCurrentPartName = (workbook, index) => {
    const partKey = `Part${index + 1}/Type`; 
    const partName = workbook[partKey] || `Part ${index + 1}`;
    setCurrentPartName(partName);
  };

  const handleNext = () => {
    setCurrentPartIndex((prevIndex) => {
      let newIndex = prevIndex + 1;
      while (newIndex < 5 && !PART_KEYS.some(key => key.startsWith(`Part${newIndex + 1}/`) && workbook[key])) {
        newIndex++;
      }
      return newIndex < 5 ? newIndex : prevIndex;
    });
  };

  const handlePrevious = () => {
    setCurrentPartIndex((prevIndex) => {
      let newIndex = prevIndex - 1;
      while (newIndex >= 0 && !PART_KEYS.some(key => key.startsWith(`Part${newIndex + 1}/`) && workbook[key])) {
        newIndex--;
      }
      return newIndex >= 0 ? newIndex : prevIndex;
    });
  };

  const randomizeName = (baseName) => `${baseName}_${Math.random().toString(36).substring(7)}`;

  const RenderWorkbookParts = ({ workbook }) => {
    const currentPartKeys = PART_KEYS.filter(
      key => key.startsWith(`Part${currentPartIndex + 1}/`) && !key.includes('/Type') // Exclude the Type part key
    );

    return (
      <div className="mane">
        {currentPartKeys.map((key, index) => {
          const content = DOMPurify.sanitize(workbook[key] || '');
          if (!content) return null;

          if (key.includes('input')) {
            return (
              <input
                key={index}
                type="text"
                name={randomizeName(key)}
                onChange={(e) => handleInteraction(e)}
                autoComplete="new-password"
                autoCorrect="off"
                autoCapitalize="none"
                spellCheck="false"
              />
            );
          } else if (key.includes('.Warm-up')) {
            return <div className='warming' key={index} dangerouslySetInnerHTML={{ __html: content }} />;
          } else if (key.includes('.task')) {
            return <h2 key={index} dangerouslySetInnerHTML={{ __html: content }} />;
          } else {
            return <div key={index} className='part' dangerouslySetInnerHTML={{ __html: content }} />;
          }
        })}
      </div>
    );
  };

  if (!workbook) {
    return <div>Loading...</div>;
  }

  return (
    <div>
      <div className='lp-top turk-red'>
        <div className='lp-top-crsmod'>
          <div className='uppercase'>{workbook.ModuleID}</div>
          <div>{workbook.moduleName}</div>
        </div>

        <div className='wide'>
          <h1>{currentPartName}</h1>
        </div>
      </div>
      
      <div className='lesson-page'>
        <div className="navigationL" onClick={handlePrevious} disabled={currentPartIndex === 0}>
          <span className="material-symbols-outlined">arrow_back</span>
          <div className="nav-dir">Back</div>
        </div>

        <RenderWorkbookParts workbook={workbook} />

        <div className="navigationR" onClick={handleNext} disabled={currentPartIndex === 4}>
          <span className="material-symbols-outlined">arrow_forward</span>
          <div className="nav-dir">Next</div>
        </div>
      </div>
    </div>
  );
};

export default WorkbookPage;
